$nav-color: #F17829;

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    font-family: 'Exo', sans-serif;
    background-color: #EEEEEE;
}

.nav-container, .login-section{
    display: flex;
    align-items: center;
}

.nav-container{
    padding: 2em 2.5em;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    background-color: $nav-color;
    color: white;
}

.login-section{
    margin-left: auto;
}

.login{
    margin-right: 0.5em;
    padding-right: 0.5em;
    cursor: pointer;
    border-right: 1px solid white;
}

.signup{
    cursor: pointer;
}

.login-container{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 0.4em;
    margin: auto;
    margin-top: 2em;
    width: 400px;
    padding-top: 1.5em;
    padding-bottom: 1.5em;
    background-color: white;

    .error-signup{
        color: red;
        text-align: center;
        font-size: 0.8rem;
        margin-bottom: 1rem;
        font-weight: bold;
    }

    .error-login{
        color: red;
        text-align: center;
        font-size: 0.8rem;
        width: 300px;
        margin: auto;
        margin-bottom: 1rem;
        font-weight: bold;
    }

}

.cat{
    height: 75px;
    width: 75px;
    border-radius: 3em;
    display: flex;
    margin: auto;
    margin-bottom: 1em;
}

.email-container, .pw-container, .user-container{
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 250px;
    margin: auto;
}

.email, .pw, .user{
    padding: 0.3em;
    width: 250px;
    margin-bottom: 1.5em;
    margin-top: 0.2em;
    border: 1px solid #D8D8D8;
    border-radius: 0.3em;
}

label{
    font-weight: 700;
}

.login-button{
    width: 250px;
    display: flex;
    justify-content: center;
    margin: auto;
    margin-bottom: 1.5em;
    padding: 0.8em 0em;
    cursor: pointer;
    border-style: none;
    border-radius: 0.3em;
    color: $nav-color;
    background-color: white;
    border: 1px solid $nav-color;
    font-family: 'Exo', sans-serif;
    font-weight: 700;
}

.login-button:hover{
    background-color: $nav-color;
    color: white;
}

.member{
    display: flex;
    align-items: left;
    justify-content: bottom;
    width: 250px;
    margin: auto;
    font-size: 0.8em;
}

.signup-member{
    margin-left: 0.3em;
}

.signup-member-button{
    font-size: 0.8em;
    color: $nav-color;
    text-decoration: none;
}

.nav-buttons{
    color: white;
    text-decoration: none;
}

